.body {
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  align-content: start;
  min-height: 100%;
  margin: 0;
  padding: 0;
  font-size: 16px;
  font-family: "Blogger Sans", "Arial", sans-serif;
  font-weight: 400;
  line-height: 1.375;
  color: #ffffff;
}
@media (max-width: 767px) {
  .body {
    overflow-x: hidden;
  }
}
@media (min-width: 768px) {
  .body {
    font-size: 28px;
    line-height: 1.17857;
  }
}
a {
  text-decoration: none;
}
img {
  max-width: 100%;
  height: auto;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.wrapper,
.wrapper-form {
  width: 100%;
  min-width: 320px;
  margin: 0 auto;
  padding: 0 25px;
}
@media (min-width: 768px) {
  .wrapper,
  .wrapper-form {
    min-width: 768px;
    padding: 0 40px;
  }
}
@media (min-width: 1440px) {
  .wrapper,
  .wrapper-form {
    max-width: 1440px;
    padding: 0 60px;
  }
}
@media (min-width: 768px) {
  .wrapper-form {
    min-width: 768px;
    padding: 0 75px;
  }
}
@media (min-width: 1440px) {
  .wrapper-form {
    max-width: 1440px;
    padding: 0 60px;
  }
}
.main-sub-title {
  font-size: 24px;
  line-height: 1;
  font-weight: 700;
  color: #ff8d30;
}
@media (min-width: 768px) {
  .main-sub-title {
    font-size: 45px;
  }
}
.zero-intervals {
  margin: 0;
  padding: 0;
}
.button {
  display: inline-flex;
  min-height: 40px;
  justify-content: center;
  align-items: center;
  font-size: 18px;
  line-height: 1;
  font-weight: 700;
  color: #1d2e5b;
  background-color: #ffd74b;
  border-radius: 10px;
  cursor: pointer;
  outline: 0;
}
.button__text {
  width: 100%;
  background: url(../img/button-arrow.png) center right 5% no-repeat;
}
@media (min-width: 1440px) {
  .button__text {
    background: url(../img/button-arrow.png) center right 10% no-repeat;
  }
}
.button:hover,
.button:focus,
.button:active {
  background-color: #ffbd30;
}
.button:active .button__text {
  opacity: 0.3;
}
.header {
  padding-top: 19px;
  background-color: #161c35;
}
@media (min-width: 768px) {
  .header__logo-ico {
    width: 166px;
    height: 42px;
  }
}
@media (min-width: 1440px) {
  .header__logo-ico {
    width: 200px;
    height: 52px;
  }
}
.social {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  max-width: 140px;
  margin: 0;
  padding: 0;
  list-style: none;
}
@media (min-width: 768px) {
  .social {
    max-width: 200px;
  }
}
.social__item {
  position: relative;
  width: 45px;
  height: 45px;
  margin-bottom: 5px;
  background-color: #ffd74b;
  border-radius: 50%;
}
@media (min-width: 768px) {
  .social__item {
    width: 62px;
    height: 62px;
  }
}
.social__item:hover,
.social__item:focus,
.social__item:active {
  background-color: #ffbd30;
}
.social__link {
  display: block;
  width: 45px;
  height: 45px;
}
@media (min-width: 768px) {
  .social__link {
    width: 62px;
    height: 62px;
  }
}
.social__item:active .social__link:active {
  opacity: 0.3;
}
.social__ico {
  position: absolute;
}
.social__item:first-of-type .social__ico {
  top: 10px;
  left: 19px;
}
@media (min-width: 768px) {
  .social__item:first-of-type .social__ico {
    top: 16px;
    left: 24px;
    width: 13px;
    height: 25px;
  }
}
@media (min-width: 1440px) {
  .social__item:first-of-type .social__ico {
    top: 16px;
    left: 25px;
    width: 13px;
    height: 27px;
  }
}
.social__item:nth-of-type(2) .social__ico {
  top: 16px;
  left: 12px;
}
@media (min-width: 768px) {
  .social__item:nth-of-type(2) .social__ico {
    top: 22px;
    left: 17px;
    width: 27px;
    height: 17px;
  }
}
@media (min-width: 1440px) {
  .social__item:nth-of-type(2) .social__ico {
    top: 23px;
    left: 16px;
    width: 30px;
    height: 15px;
  }
}
.social__item:nth-of-type(3n) .social__ico {
  top: 13px;
  left: 14px;
}
@media (min-width: 768px) {
  .social__item:nth-of-type(3n) .social__ico {
    top: 20px;
    left: 21px;
    width: 20px;
    height: 20px;
  }
}
@media (min-width: 1440px) {
  .social__item:nth-of-type(3n) .social__ico {
    top: 19px;
    left: 20px;
    width: 22px;
    height: 22px;
  }
}
.our-contacts {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-self: flex-start;
  margin: 0;
  padding: 0;
  list-style: none;
}
@media (min-width: 1440px) {
  .our-contacts {
    display: none;
  }
}
.our-contacts__item {
  position: relative;
  padding: 11px 0 11px 61px;
}
@media (min-width: 768px) {
  .our-contacts__item {
    padding: 20px 0 20px 90px;
  }
}
@media (min-width: 1440px) {
  .our-contacts__item {
    padding: 31px 0 31px 121px;
    margin-right: 111px;
  }
}
.our-contacts__item::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 45px;
  height: 45px;
  border-radius: 50%;
  background-color: #121936;
}
@media (min-width: 768px) {
  .our-contacts__item::before {
    width: 68px;
    height: 68px;
  }
}
@media (min-width: 1440px) {
  .our-contacts__item::before {
    width: 90px;
    height: 90px;
  }
}
.our-contacts__link {
  position: relative;
  color: #192144;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .our-contacts__link {
    line-height: 1;
  }
}
.our-contacts__link--check-out {
  color: #ffd74b;
  text-transform: uppercase;
}
.our-contacts__link-ico-phone,
.our-contacts__link-ico-mail {
  position: absolute;
  fill: #ffd74b;
}
.our-contacts__link-ico-phone {
  top: 0;
  left: -47px;
}
@media (min-width: 768px) {
  .our-contacts__link-ico-phone {
    top: 0;
    left: -69px;
    width: 27px;
    height: 25px;
  }
}
@media (min-width: 1440px) {
  .our-contacts__link-ico-phone {
    top: -2px;
    left: -94px;
    width: 36px;
    height: 34px;
  }
}
.our-contacts__link-ico-mail {
  top: -3px;
  left: -50px;
}
@media (min-width: 768px) {
  .our-contacts__link-ico-mail {
    top: -5px;
    left: -73px;
    width: 35px;
    height: 37px;
  }
}
@media (min-width: 1440px) {
  .our-contacts__link-ico-mail {
    top: -9px;
    left: -100px;
    width: 47px;
    height: 50px;
  }
}
.our-contacts__link-ico-phone--check-out,
.our-contacts__link-ico-mail--check-out {
  fill: currentColor;
}
.our-contacts__link--check-out:focus,
.our-contacts__link--check-out:hover,
.our-contacts__link--check-out:active {
  color: #ffbd30;
}
.our-contacts__link--check-out:active {
  opacity: 0.3;
}
.companion {
  position: relative;
  background: url(../img/background/companion-mobile.png) 0 68px no-repeat, #192144;
}
@media (min-resolution: 2dppx) {
  .companion {
    background: url(../img/background/companion-mobile@2x.png) 0 68px no-repeat, #192144;
  }
}
@media (min-width: 768px) {
  .companion {
    background: url(../img/background/companion-tablet.png) 0 147px no-repeat, #192144;
  }
}
@media (min-width: 768px) and (min-resolution: 2dppx) {
  .companion {
    background: url(../img/background/companion-tablet@2x.png) 0 147px no-repeat, #192144;
  }
}
@media (min-width: 1440px) {
  .companion {
    background: url(../img/background/companion-desktop.png) 0 180px no-repeat, #192144;
  }
}
@media (min-width: 1440px) and (min-resolution: 2dppx) {
  .companion {
    background: url(../img/background/companion-desktop@2x.png) 0 180px no-repeat, #192144;
  }
}
.companion::after {
  content: "";
  position: absolute;
  top: 100%;
  left: 0;
  z-index: 1;
  width: 100%;
  height: 31px;
  background-color: #192144;
  border-radius: 0 0 20px 20px;
}
@media (min-width: 768px) {
  .companion::after {
    height: 61px;
    border-radius: 0 0 35px 35px;
  }
}
@media (min-width: 1440px) {
  .companion::after {
    top: 99%;
    height: 63px;
    border-radius: 0 0 35px 35px;
  }
